@import "resourse/normalize.css";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* button {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

body {
  min-width: 320px;
  min-height: 100vh;
  background-color: #fff;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  font-style: normal;
  color: #343A40;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
p,
button {
  margin: 0;
  padding: 0;
  outline: none;
  color: h1, h2, h3, h4, h5, p, button headingColor;
}

h1 {
  font-size: 56px;
}

h2 {
  font-size: 48px;
}

h3 {
  font-size: 40px;
}

h4 {
  font-size: 32px;
}

h5 {
  font-size: 26px;
}

img {
  max-width: 100%;
}

svg {
  fill: inherit;
  max-width: 100%;
}

svg path {
  fill: inherit;
}

input {
  outline: none;
  border-color: transparent;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  box-sizing: border-box;
}

.section-heading {
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 130%;
  color: #212529;
}

.btn-primary {
  padding: 10px 20px;
  background-color: #D22020;
  color: #fff;
  font-weight: 500;
  border-radius: 50px;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  transition-property: color, opacity;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.btn-primary:hover {
  opacity: 0.7;
}

.btn-primary:active {
  opacity: 1;
  color: #000;
}

.btn-primary:focus {
  opacity: 1;
  outline: 1px solid #D22020;
  outline-offset: 5px;
}

.btn-light {
  padding: 20px 70px;
  background-color: transparent;
  color: #D22020;
  border-radius: 50px;
  border: 1px solid #D22020;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.btn-light:hover {
  background-color: #D22020;
  color: #fff;
}

.btn-light:focus {
  color: #fff;
  background-color: #D22020;
  outline: 1px solid #D22020;
  outline-offset: 5px;
}

.btn-light:active {
  background-color: transparent;
  color: #000;
}

@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Inter";
  font-display: swap;
  src: local("Inter-Regular"), url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-style: medium;
  font-family: "Inter";
  font-display: swap;
  src: local("Inter-Medium"), url("../fonts//Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}
@font-face {
  font-weight: 600;
  font-style: bold;
  font-family: "Inter";
  font-display: swap;
  src: local("Inter-SemiBold"), url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}
.header {
  right: 0;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 100;
  background-color: #fff;
  color: #000;
  font-size: 16px;
  line-height: 150%;
}
.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}
.header__logo {
  max-height: 45px;
}
.header__menu {
  width: 38%;
  padding: 0;
}
.header__link {
  padding: 10px 0;
  border-bottom: 1px solid transparent;
  transition-property: color, border;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.header__link:hover {
  border-bottom: 1px solid #D22020;
  color: #D22020;
}
.header__link:focus {
  outline: none;
  border-bottom: 1px solid #D22020;
  color: #D22020;
}
.header__link:active {
  border-bottom: 1px solid transparent;
  color: #D22020;
}

.menu-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.burger-btn {
  display: none;
}

.burger-close-btn {
  display: none;
}

.hero {
  margin-top: 80px;
}
.hero-container {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  min-height: 945px;
  background-image: linear-gradient(rgb(255, 255, 255) 0%, rgba(255, 255, 255, 0) 35%), url(../img/hero.png);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: auto;
}
.hero__description {
  max-width: 210px;
}
.hero__contacts {
  position: absolute;
  bottom: 34px;
  right: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hero__link-arrow {
  position: absolute;
  bottom: 30px;
  left: 50%;
  animation: arrow 3s linear infinite;
}
.hero__link-arrow svg {
  width: 20px;
  height: 20px;
  fill: #ADB5BD;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.hero__link-arrow:hover svg {
  fill: #D22020;
}
.hero__link-arrow:focus svg {
  fill: #D22020;
}

.contacts-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  letter-spacing: 0.02em;
  font-size: 18px;
  transition-property: color, fill;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.contacts-link svg {
  fill: #8D959D;
  width: 24px;
  height: 24px;
  margin-right: 11px;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.contacts-link:hover {
  color: #D22020;
}
.contacts-link:hover svg {
  fill: #D22020;
}

.contacts-link:focus {
  color: #D22020;
}
.contacts-link:focus svg {
  fill: #D22020;
}

.contacts-link:active {
  color: #000;
}
.contacts-link:active svg {
  fill: #D22020;
}

.contacts-link:not(:last-of-type) {
  margin-right: 40px;
}

@keyframes arrow {
  0% {
    bottom: 30px;
  }
  50% {
    bottom: 60px;
  }
  100% {
    bottom: 30px;
  }
}
.portfolio {
  padding-top: 56px;
  padding-bottom: 80px;
  scroll-margin-top: 5rem;
}
.portfolio__header {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.portfolio__header-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #D22020;
  border-bottom: 1px solid transparent;
  transition-property: border;
  transition-duration: 0.2s;
  transition-timing-function: linear;
  letter-spacing: 0.05m;
}
.portfolio__header-link span {
  margin-right: 14px;
  font-size: 18px;
}
.portfolio__header-link-svg {
  fill: #D22020;
  stroke: transparent;
  transform: rotate(270deg);
  width: 16px;
  height: 16px;
}
.portfolio__header-link:hover {
  border-bottom: 1px solid #D22020;
}
.portfolio__list {
  display: grid;
  margin: 0;
  grid-gap: 27px;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 76px;
}
.portfolio__list-link {
  right: 0;
  top: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 9px;
  z-index: 10;
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.portfolio__list-link:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.portfolio__list-link:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.portfolio__list-link:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0);
}
.portfolio__list-item {
  position: relative;
}
.portfolio__list-item-heading {
  font-weight: 500;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: -0.03em;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-bottom: 4px;
}
.portfolio__list-item-description {
  font-size: 18px;
}
.portfolio__list-item-img {
  position: relative;
  border-radius: 8px;
  min-height: 515px;
  height: 80%;
  margin-bottom: 12px;
}
.portfolio__list-item-img span {
  position: absolute;
  bottom: 16px;
  left: 16px;
  color: #fff;
  background: #212529;
  border-radius: 32px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.02em;
  padding: 2px 8px;
}
.portfolio__list-item-tag {
  position: absolute;
  bottom: 16px;
  left: 16px;
  color: #fff;
  background: #212529;
  border-radius: 32px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.02em;
  padding: 2px 8px;
  z-index: 20;
  cursor: pointer;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.portfolio__list-item-tag:hover {
  background-color: #fff;
  color: #212529;
}
.portfolio__btn {
  display: block;
  margin: 0 auto;
}

.about {
  scroll-margin-top: 5.5rem;
  padding-top: 30px;
  padding-bottom: 37px;
}
.about__heading {
  margin-bottom: 55px;
}
.about__workers {
  margin-bottom: 57px;
}
.about__workers-heading {
  display: inline-block;
  font-weight: 400;
  font-size: 32px;
  line-height: 120%;
}
.about__workers-heading span {
  color: #D22020;
}
.about__workers-heading:not(:last-child) {
  margin-right: 25px;
}
.about__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: stretch;
}
.about__content-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  width: 50%;
  max-height: 505px;
  margin-right: 20px;
}
.about__content-main-description {
  font-size: 18px;
  line-height: 150%;
}
.about__content-main-achievements {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: stretch;
  justify-content: flex-start;
}
.about__content-main-achievement:not(:last-child) {
  margin-right: 34px;
}
.about__content-main-achievement {
  max-width: 140px;
}
.about__content-main-achievement span {
  display: inline-block;
  font-weight: 600;
  font-size: 40px;
  line-height: 120%;
  letter-spacing: -0.03em;
  color: #D22020;
  margin-bottom: 20px;
}
.about__content-photo {
  text-align: center;
  width: 50%;
}
.about__content-photo-img {
  cursor: pointer;
}
.about__content-photo-description {
  color: #8D959D;
}
.about__btn {
  align-self: flex-start;
}

.request {
  scroll-margin-top: 5rem;
  padding-bottom: 155px;
}
.request-heading {
  margin-bottom: 50px;
}
.request__contacts {
  width: 36%;
}
.request__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: initial;
  flex-wrap: wrap;
}
.request__manager-avatar {
  margin-bottom: 30px;
}
.request__manager-name {
  margin: 0;
  margin-bottom: 8px;
  letter-spacing: -0.06em;
}
.request__manager-descr {
  display: inline-block;
  font-size: 18px;
}
.request__links-heading {
  margin-bottom: 17px;
  letter-spacing: -0.03em;
  line-height: 130%;
}
.request__links-group {
  color: #D22020;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
}
.request__link:not(:last-child) {
  margin-bottom: 10px;
}
.request__link {
  font-size: 18px;
  line-height: 30px;
  display: inline-block;
  border-bottom: 1px solid rgba(210, 32, 32, 0.4);
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.request__link svg {
  width: 16px;
  height: 16px;
  transform: rotate(230deg);
  fill: rgba(210, 32, 32, 0.4);
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.request__link:hover {
  background-color: #D22020;
  color: #fff;
}
.request__link:hover svg {
  fill: #fff;
}
.request__link:focus {
  background-color: #D22020;
  color: #fff;
}
.request__link:focus svg {
  fill: #fff;
}
.request__link:active {
  background-color: transparent;
  color: #000;
  border-color: #000;
}
.request__link:active svg {
  fill: #000;
}
.request__form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
}
.request__form-block {
  width: 66%;
}
.request__form-input::placeholder {
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #ADB5BD;
}
.request__form-label {
  color: #ADB5BD;
  width: 62%;
  margin-bottom: 60px;
}
.request__form-input {
  width: 100%;
  padding: 12px 0 8px 0;
  border-bottom: 1px solid #CED4DA;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.request__form-input:hover {
  background: rgba(0, 0, 0, 0.03);
}
.request__form-textarea::placeholder {
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #ADB5BD;
}
.request__form-textarea {
  width: 100%;
  min-height: 150px;
  border: none;
  border-bottom: 1px solid #CED4DA;
  margin-bottom: 30px;
}
.request__btn {
  cursor: pointer;
  padding: 16px 32px;
  font-weight: 500;
  font-size: 24px;
}

.footer {
  padding-top: 25px;
  padding-bottom: 25px;
  border-top: 1px solid #ACB5BD;
}
.footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.footer__link:not(:last-child) {
  margin-right: 20px;
}
.footer__link {
  color: #ADB5BD;
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.footer__link:hover {
  color: #D22020;
}
.footer__link:focus {
  color: #D22020;
}
.footer__link:active {
  color: #000;
}
.footer__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.footer__logo-img {
  filter: grayscale(1);
  transition-property: filter;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.footer__logo-img:hover {
  filter: grayscale(0);
}

.container {
  padding-left: 30px;
  padding-right: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

.modal {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  border-radius: 3px;
  transition: 0.3s all;
}

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

.js-modal-close {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #fff;
  cursor: pointer;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.js-modal-close:hover {
  fill: #D22020;
}

.js-modal-close:focus {
  fill: #D22020;
}

.js-modal-close:active {
  fill: #000;
}

@media (max-width: 1280px) {
  .header__menu {
    width: 50%;
  }
  .about__btn {
    align-self: auto;
  }
  .about__content {
    flex-direction: column;
  }
  .about__content-photo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
  }
  .about__content-main {
    height: auto;
    width: 100%;
    margin-right: 0;
    align-items: center;
    margin-bottom: 30px;
  }
  .about__content-main-achievements {
    margin-bottom: 20px;
  }
  .about__content-main-description {
    margin-bottom: 20px;
  }
  .about__workers {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 1024px) {
  .header__menu {
    width: 70%;
  }
  .about__content-main {
    max-height: none;
  }
  .request__links {
    margin-top: 20px;
  }
  .modal {
    width: 66%;
  }
}
@media (max-width: 768px) {
  .modal {
    width: 90%;
  }
  .header-container {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .header__menu {
    height: 0px;
    overflow: hidden;
    transition-property: height;
    transition-duration: 0.15s;
    transition-timing-function: linear;
  }
  .header__btn {
    display: none;
  }
  .menu-list {
    margin-top: 40px;
    flex-direction: column;
    align-items: flex-start;
  }
  .menu-item {
    margin-bottom: 20px;
  }
  .burger-btn {
    position: absolute;
    top: 25px;
    right: 30px;
    display: block;
    fill: #000;
  }
  .burger-btn svg {
    height: 16px;
    width: 24px;
    transition-property: fill;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }
  .burger-btn:active svg {
    fill: #D22020;
  }
  .burger-close-btn {
    position: absolute;
    top: 25px;
    right: 30px;
    display: block;
    fill: #000;
  }
  .burger-close-btn svg {
    height: 16px;
    width: 24px;
    transition-property: fill;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }
  .burger-close-btn:active svg {
    fill: #D22020;
  }
  .contacts-link:not(:last-child) {
    margin-right: 10px;
  }
  .contacts-link svg {
    margin-right: 0;
    width: 48px;
    height: 48px;
  }
  .contacts-link-name {
    display: none;
  }
  .hero__link-arrow svg {
    width: 30px;
    height: 30px;
  }
  .portfolio__list {
    grid-gap: 35px;
    grid-template-columns: 1fr;
  }
  .portfolio__list-item-img {
    min-height: 400px;
  }
  .hidden {
    position: absolute;
    visibility: hidden;
    height: 0;
    width: 0;
  }
  .request {
    padding-bottom: 80px;
  }
  .request__manager {
    margin-bottom: 30px;
  }
  .request__main {
    flex-direction: column;
    align-items: center;
  }
  .request__contacts {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .request__form {
    align-items: center;
  }
  .request__form-block {
    width: 100%;
  }
  .request__form-label {
    width: 100%;
  }
}
@media (max-width: 564px) {
  .portfolio__heading {
    margin-bottom: 20px;
  }
  .portfolio__header {
    align-items: flex-start;
    flex-direction: column;
  }
  .about__btn {
    align-self: center;
  }
  .about__heading {
    margin-bottom: 30px;
  }
  .about__workers {
    margin-bottom: 30px;
  }
  .about__workers-heading {
    margin: 0;
    margin-bottom: 30px;
  }
  .about__content-main {
    align-items: flex-start;
  }
  .about__content-main-achievements {
    flex-direction: column;
    align-items: flex-start;
  }
  .request__btn {
    width: 100%;
  }
  .footer__links {
    flex-direction: column;
  }
  .footer__link:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.burger-active {
  position: static;
  width: auto;
  height: 500px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: auto;
  overflow: visible;
  transition-property: height;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.visually-visible {
